body {
  background: #fff;
  font-size: 18px;
  font-family: "proximanova-regular", sans-serif;
  line-height: 1;
}

.inner {
  max-width: 1230px;
  padding: 0 30px;
  margin: 0 auto;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.main {
  overflow: hidden;
}

.services {
  background-image: url("../img/logo-shape.svg");
  background-repeat: no-repeat;
  background-position: center top 180px;
  background-color: #233148;
  padding-top: 180px;
  padding-bottom: 250px;
}

.our {
  background-color: #EAEFF5;
  padding-bottom: 360px;
}

.our .btn {
  background-color: #FCCF18;
  padding: 20px 130px;
  cursor: pointer;
  font-family: "proximanova-bold", sans-serif;
  font-size: 16px;
  line-height: 1.18;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  align-self: baseline;
}

.our__infoblock {
  width: 50%;
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1025px) {
  .our__infoblock {
    flex-direction: column;
    width: auto;
  }
}

.our__field {
  display: flex;
  width: 100%;
  margin-top: 35px;
  justify-content: center;
  position: relative;
  top: 29px;
}

.our__desc {
  margin-top: 45px;
  max-width: 200px;
  font-family: "proximanova-regular", sans-serif;
  font-size: 24px;
  line-height: 1.4;
  color: #fff;
  margin-left: 63px;
}

@media only screen and (max-width: 1025px) {
  .our__desc {
    margin-left: 0;
  }
}

.our__wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding-top: 70px;
  margin-bottom: 200px;
  background-color: #1F65D9;
  position: relative;
  top: -110px;
  background-image: url("../img/bg-docs.svg");
  background-repeat: no-repeat;
  background-position: left 0 top 80px;
}

.our__wrapper .title {
  font-family: "proximanova-xbold", sans-serif;
}

.our__wrapper .book-pic {
  position: absolute;
  top: -73px;
  right: 0;
  z-index: 9999;
}

@media only screen and (max-width: 1280px) {
  .our__wrapper .book-pic {
    right: auto;
    left: 60%;
  }
}

@media only screen and (max-width: 992px) {
  .our__wrapper .book-pic {
    left: 35%;
  }
}

.our__wrapper:before, .our__wrapper:after {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #1F65D9;
}

.our__wrapper:before {
  right: 100%;
}

.our__wrapper:after {
  left: auto;
  right: 100%;
}

@media only screen and (max-width: 1440px) {
  .our__wrapper:after {
    left: 100%;
  }
}

.items {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "proximanova-black", sans-serif;
  margin-top: 120px;
}

@media only screen and (max-width: 768px) {
  .items {
    margin-top: 60px;
  }
}

.items .item {
  width: 50%;
  padding: 0 45px;
  display: flex;
  text-align: left;
  flex-direction: column;
  margin-bottom: 100px;
}

@media only screen and (max-width: 992px) {
  .items .item {
    padding: 0 25px;
  }
}

@media only screen and (max-width: 768px) {
  .items .item {
    width: 100%;
    margin-bottom: 50px;
  }
}

.items .item__inner {
  padding-left: 55px;
}

@media only screen and (max-width: 1024px) {
  .items .item__inner {
    padding-left: 0;
  }
}

.items .item__icon {
  color: #1F65D9;
  line-height: 0.6;
  font-size: 52px;
}

.items .item__icon svg {
  stroke: currentColor;
  fill: currentColor;
}

.items .item__title {
  color: #fff;
  font-size: 32px;
  font-family: "proximanova-bold", sans-serif;
  line-height: 1.21;
  letter-spacing: 0.60px;
  margin-top: 25px;
  margin-bottom: 50px;
}

.items .item__text {
  color: #8796AE;
  font-family: "proximanova-light", sans-serif;
  font-size: 18px;
  line-height: 1.55;
  letter-spacing: 0.36px;
}
